import { useState, useRef, useEffect } from "react";
import classes from "./Home.module.scss";

const videosList = [
  "/homeVideo.mp4",
  "/homeVideo2.mp4",
  "/homeVideo3.mp4",
  "/homeVideo4.mp4",
];

function Home({ mouseClicked, setMouseClicked, isMobile }) {
  const [video, setVideo] = useState(1);
  const [isPlay, setIsPlay] = useState(false);
  const videoRef = useRef();

  useEffect(() => {
    if (video >= 0) {
      videoRef.current.load();
      // setTimeout(() => videoRef.current.play(), 300);
    }
  }, [video]);

  useEffect(() => {
    if (mouseClicked) {
      videoRef.current.play();
      setIsPlay(true);
    }
  }, [mouseClicked]);

  function stopOrPlay() {
    if (isPlay) {
      videoRef.current.pause();
      setIsPlay(false);
    } else {
      videoRef.current.play();
      setIsPlay(true);
    }
  }

  function playVideo() {
    if (isPlay) {
      videoRef.current.play();
    }
  }

  function nextVideo() {
    setVideo((video + 1) % videosList.length);
  }

  function previousVideo() {
    setVideo((video - 1 + videosList.length) % videosList.length);
  }

  function revealHome() {
    if (!mouseClicked) {
      setMouseClicked(true);
    }
  }

  let pathClass = mouseClicked
    ? `${classes.path} ${classes.animationOut}`
    : `${classes.path} ${classes.animationIn}`;

  let playPause =
    isPlay === true ? "url('/Icons/pause.png')" : "url('/Icons/play.png')";
  let videoElement = (
    <div className={classes.videoWrapper}>
      <video
        className={classes.video}
        // style={{ objectPosition: isMobile ? "20% center" : "center" }}
        ref={videoRef}
        onLoadedData={playVideo}
        autoPlay={false}
        loop
      >
        <source src={videosList[video]} type="video/mp4" />
      </video>
      {/* <div className={classes.background}></div> */}
      <div
        className={classes.button1}
        onClick={previousVideo}
        style={{
          maskImage: "url('/Icons/newLeft.png')",
          webkitMaskImage: "url('/Icons/newLeft.png')",
        }}
      ></div>
      <div
        className={classes.button2}
        onClick={nextVideo}
        style={{
          maskImage: "url('/Icons/newRight.png')",
          webkitMaskImage: "url('/Icons/newRight.png')",
        }}
      ></div>
      <div
        className={classes.playPause}
        onClick={stopOrPlay}
        style={{
          maskImage: playPause,
          webkitMaskImage: playPause,
        }}
      ></div>
    </div>
  );

  return (
    <div className={classes.home}>
      {/* {/* <div
        className={classes.websiteGate}
        onClick={revealHome}
        style={{
          backgroundImage: isMobile
            ? "url('/pictures/homeMobilePicture.jpg')"
            : "url('/pictures/homePicture.jpg')",
          left: mouseClicked ? "-100%" : "0",
          backgroundPosition: "left",
        }}
      ></div> */}
      <div
        className={classes.websiteGate}
        onClick={revealHome}
        style={{ opacity: mouseClicked ? 0 : 1, zIndex: mouseClicked ? 0 : 1 }}
      >
        <svg width="100%" height="100%" xmlns="http://www.w3.org/2000/svg">
          <path
            d="M28.44,11.23C32,26,35.57,40.86,35.38,56S30.79,86.79,20.32,97.73c-5.1,5.33-15.13,8.74-18.9,2.4-2.94-4.95,1.62-11.6,7.2-13s11.38.7,16.78,2.69c11.76,4.33,24,8.3,36.54,7.63S87.4,90.54,92.73,79.19c-8-6.75-22.49.86-22.39,11.36s13.11,18.06,22.74,13.88,13.4-17.5,8.37-26.71c-2.43,8.88-1,20.5,7.5,24.12,7.92,3.38,17.28-2.93,20.36-11s1.64-17,.17-25.52L118.17.09a674.48,674.48,0,0,0,12,116.53,60.91,60.91,0,0,1,6.23-48.93c2.26-3.7,5.78-7.54,10.08-7,4.07.49,6.72,5,6.29,9.13s-3.17,7.55-6.29,10.21a34.31,34.31,0,0,1-26.82,7.7A23.55,23.55,0,0,1,142,92.94c7.54,7,11.62,19.67,21.89,20.51,7.23.58,13.49-5.94,15.2-13s0-14.43-1.64-21.49c-.91-3.84-2.78-8.52-6.73-8.68-1.24,13.71-1.78,29.46,8.14,39,9.72,9.36,27.24,7.75,36.11-2.42s8.69-26.59.7-37.47c-1.93-2.62-5.64-5.1-8.15-3A6.1,6.1,0,0,0,205.87,69c-8.32,22.07,7.84,48.36,30.33,55.48,7.76,2.46,16.61,3,23.7-1s11.35-13.49,7.63-20.72"
            className={pathClass}
          />
          <path
            d="M168.24,218.14a50.12,50.12,0,0,0-30.78-38c-4.09-1.58-8.53-2.62-12.85-1.88-4.52.77-8.52,3.44-11.85,6.59-17.16,16.25-17.5,44.56-7.22,65.84S134.4,287.81,152.47,303a76.94,76.94,0,0,0,23.07-59.46C168.75,259.05,156.21,273,139.79,277s-36.24-4.7-40.74-21c28.82-10.6,57.52,24.19,87.85,19.37,15.88-2.53,28.31-15.91,34.27-30.84s6.62-31.37,7.2-47.44c.43-11.95-1.9-27.59-13.66-29.74a6.74,6.74,0,0,0-5.64,1.11c-2,1.7-2.11,4.65-2.11,7.25l.35,112c0,7.35.35,15.51,5.49,20.78,5.66,5.81,15.53,5.7,22.54,1.62s11.68-11.23,15.33-18.49c8-15.84,11.93-35.89,2.16-50.68-13.24,8.24-15.32,29.66-3.92,40.3,5.74,5.34,14.32,8.63,16.52,16.15,6.15-1.86,6.56-10.15,7.58-16.5,3-18.77,24.15-32.4,42.48-27.39-17.39-3.87-35.89,13.41-33.21,31,1.78,11.71,11.52,21.46,23,24.43s24.09-.4,33.41-7.71c4-3.17,7.6-7.16,9.4-12,2.7-7.25,1.13-15.3-.52-22.85q-9.72-44.58-21.85-88.61c-15.9,17.87-9.16,45.6-1.53,68.27Q338.6,288.94,353,331.75c-3-29.14-5.37-60.94,10.78-85.38,1.3-2,2.86-4,5.1-4.67,2.08-.66,4.33-.07,6.39.65a47.62,47.62,0,0,1,26.44,23.51A58.63,58.63,0,0,1,349.39,279c7.54-5.28,18.18-.66,24.67,5.85s11.34,15,19.39,19.42a24.56,24.56,0,0,0,35.38-28.58"
            className={pathClass}
          />
        </svg>
      </div>
      {videoElement}
    </div>
  );
}

export default Home;
